import Vue from 'vue'
import Router from 'vue-router'

import Home from '../pages/SearchPage'
import Help from '../pages/Help'
import About from '../pages/AboutUs'

Vue.use(Router)

const routerObj = {
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/help',
      name: 'help',
      component: Help
    },
    {
      path: '/about',
      name: 'about',
      component: About
    }
  ]
}

const router = new Router(routerObj);

export default router;
